import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import Typography from '@material-ui/core/Typography';
import MenuItem from '@material-ui/core/MenuItem';
import Input from '@material-ui/core/Input';
import Divider from '@material-ui/core/Divider';
import { notifySuccess, notifyError } from '../../actions/notification';
import BlankProfile from '../../assets/blankprofile.png';
import {
  fetchAllUsers,
  fetchAddAdmin,
  fetchDeleteAdmin,
  fetchMaxProjects,
} from '../../actions/api/users';
import { allUsers } from '../../selectors/users';
import { removeDiacritics } from '../../utils';

const styles = {
  root: {
    backgroundColor: '#f0f0f0',
    flex: 1,
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
  },
  formTitle: {
    paddingLeft: '2.5%',
    paddingTop: '3%',
  },
  formInput: {
    paddingLeft: '5%',
    paddingRight: '5%',
    paddingBottom: '5%',
  },
  grid: {
    paddingLeft: '2.5%',
    paddingTop: '2%',
    display: 'grid',
    'grid-template-columns': 'repeat(4, 1fr)',
    'grid-gap': '50px',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  titleText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'bold',
  },
};

class UserList extends Component {
  constructor(props) {
    super(props);
    this.typeChange = this.typeChange.bind(this);
    this.maxChange = this.maxChange.bind(this);
  }

  shouldComponentUpdate(nextProps) {
    if (nextProps.users.length !== this.props.users.length) {
      return true;
    }
    for (let i = 0; i < nextProps.users.length; i += 1) {
      if (nextProps.users[i] !== this.props.users[i]) {
        return true;
      }
    }
    return false;
  }

  typeChange(event) {
    const { name, value } = event.target;
    if (value === 'administrator') {
      this.props.fetchAddAdmin(name)
        .then((response) => {
          if (response.ok) {
            this.props.notifySuccess('Updated succesfully');
          }
        });
    } else {
      this.props.fetchDeleteAdmin(name)
        .then((response) => {
          if (response.ok) {
            this.props.notifySuccess('Updated succesfully');
          }
        });
    }
  }

  maxChange(event) {
    const { name, value } = event.target;
    const newValue = Number(value);
    this.props.fetchMaxProjects(name, { max_projects: newValue })
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess('Updated succesfully');
        }
      });
  }

  render() {
    const {
      classes,
      userType,
      users,
      userName,
    } = this.props;
    if (userType === 'user') {
      return (
        <Redirect to="/" />
      );
    }
    const sortorder = ['super_admin', 'administrator', 'user'];
    users.sort((a, b) => {
      if (sortorder.indexOf(a.account_type) - sortorder.indexOf(b.account_type) === 0) {
        if (removeDiacritics(a.name) < removeDiacritics(b.name)) { return -1; }
        if (removeDiacritics(a.name) > removeDiacritics(b.name)) { return 1; }
        return 0;
      }
      return sortorder.indexOf(a.account_type) - sortorder.indexOf(b.account_type);
    });
    return (
      <Fragment>
        {users.map((user) => {
          return (
            <Fragment>
              <div style={{ display: 'grid', gridTemplateColumns: '2fr 2fr 1fr', margin: '5px' }}>
                <div style={{ display: 'flex', flexDirection: 'columns' }}>
                  <div>
                    {(!user.image || user.image === 'null')
                    && (
                      <img alt="" className={classes.customIcon} src={BlankProfile} />
                    )}
                    {(user.image && user.image !== 'null')
                    && (
                      <img alt="" className={classes.customIcon} src={user.image} />
                    )}
                  </div>
                  <Typography className={classes.titleText} style={{ marginLeft: '5px' }}>
                    {user.name}
                  </Typography>
                </div>
                <div>
                  {((userType !== 'super_admin') || (user.name === userName))
                    && (
                      <Typography>
                        {user.account_type}
                      </Typography>
                    )}

                  {(userType === 'super_admin') && (user.name !== userName)
                  && (
                    <Select
                      name={user.id}
                      value={user.account_type}
                      onChange={this.typeChange}
                      input={<Input id="type" />}
                      style={{ width: '130px' }}
                    >
                      <MenuItem value="user">user</MenuItem>
                      <MenuItem value="administrator">administrator</MenuItem>
                    </Select>
                  )}
                </div>
                <div style={{ width: '35px', marginLeft: '3' }}>
                  <Input
                    type="number"
                    min="0"
                    max="99"
                    name={user.id}
                    value={user.max_projects}
                    onChange={this.maxChange}
                    step="1"
                  />
                </div>
              </div>
              <Divider />
            </Fragment>
          );
        })}
      </Fragment>
    );
  }
}

UserList.propTypes = {
  classes: PropTypes.any.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  userType: PropTypes.string.isRequired,
  userName: PropTypes.string.isRequired,
  users: PropTypes.array.isRequired,
  fetchAddAdmin: PropTypes.func.isRequired,
  fetchDeleteAdmin: PropTypes.func.isRequired,
  fetchMaxProjects: PropTypes.func.isRequired,
};

function mapStateToProps(state, ownProps) {
  return {
    userType: state.auth.accountType,
    userName: state.auth.name,
    users: allUsers(ownProps, state),
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    notifyError,
    notifySuccess,
    fetchAllUsers,
    fetchAddAdmin,
    fetchDeleteAdmin,
    fetchMaxProjects,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(UserList));
