import {
  FETCH_ALL_USERS,
  FETCH_MAX_PROJECTS,
  FETCH_ADD_ADMIN,
  FETCH_DELETE_ADMIN,
} from '../../constants/api/users';

export const fetchAllUsers = () => ({
  type: FETCH_ALL_USERS,
  fetch: true,
  url: '/users/index',
  params: {
    method: 'GET',
  },
});

export const fetchMaxProjects = (id, body) => ({
  type: FETCH_MAX_PROJECTS,
  fetch: true,
  url: `/users/${id}/max_projects`,
  params: {
    method: 'POST',
    body,
  },
});

export const fetchAddAdmin = id => ({
  type: FETCH_ADD_ADMIN,
  fetch: true,
  url: `/users/${id}/admin`,
  params: {
    method: 'POST',
  },
});

export const fetchDeleteAdmin = id => ({
  type: FETCH_DELETE_ADMIN,
  fetch: true,
  url: `/users/${id}/admin`,
  params: {
    method: 'DELETE',
  },
});
