import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import SearchIcon from '@material-ui/icons/Search';
import { Redirect } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import Tooltip from '@material-ui/core/Tooltip';
import Switch from '@material-ui/core/Switch';
import CardPanel from '../../components/lib/Card';
import UserList from '../../components/user/userList';
import { userSearch } from '../../actions/user';
import { fetchAllUsers } from '../../actions/api/users';

const styles = {
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  search: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-around',
    alignItems: 'center',
  },
  titleText: {
    color: '#2a3d41',
    fontSize: 15,
    fontWeight: 'bold',
  },
  icon: {
    alignSelf: 'center',
    color: '#2a3d41',
    '&:hover': {
      color: '#ca3921',
    },
    float: 'left',
  },
  container: {
    width: '700px',
    alignSelf: 'center',
    padding: '30px',
    marginTop: '20px',
  },
  titles: {
    display: 'grid',
    gridTemplateColumns: '2fr 2fr 1fr',
    marginBottom: '10px',
    marginTop: '20px',
  },
};

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sysrec: false,
    };
    this.handleChange = this.handleChange.bind(this);
    this.changeSysrecConfig = this.changeSysrecConfig.bind(this);
  }

  componentWillMount() {
    if (this.props.userType !== 'user') {
      this.props.fetchAllUsers();
    }
  }

  componentDidMount() {
    const URL = 'https://lessons.zmartboard.cl/config';
    fetch(URL, {
      method: 'GET',
    }).then((response) => {
      return response.ok ? response.json() : {};
    }).then((resp) => {
      if (resp.sys_rec_status === '0') {
        this.setState({ sysrec: false });
      } else if (resp.sys_rec_status === '1') {
        this.setState({ sysrec: true });
      }
    });
  }

  changeSysrecConfig(value) {
    const URL = 'https://lessons.zmartboard.cl/config';
    switch (value) {
      case true:
        fetch(URL, {
          method: 'PATCH',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            sys_rec_status: 1,
          }),
        }).then((response) => {
          return response.ok ? response.json() : {};
        }).then((resp) => {
          if (resp.sys_rec_status === '0') {
            this.setState({ sysrec: false });
          } else if (resp.sys_rec_status === '1') {
            this.setState({ sysrec: true });
          }
        });
        break;
      case false:
        fetch(URL, {
          method: 'PATCH',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            sys_rec_status: 0,
          }),
        }).then((response) => {
          return response.ok ? response.json() : {};
        }).then((resp) => {
          if (resp.sys_rec_status === '0') {
            this.setState({ sysrec: false });
          } else if (resp.sys_rec_status === '1') {
            this.setState({ sysrec: true });
          }
        });
        break;
      default:
        console.error('Error');
    }
  }

  handleChange(text) {
    this.props.userSearch({ text });
  }

  render() {
    const {
      classes,
      userType,
    } = this.props;
    if (userType === 'user') {
      return (
        <Redirect to="/" />
      );
    }
    return (
      <CardPanel className={classes.container}>
        <div className={classes.search}>
          <div style={{ display: 'flex', flexDirection: 'row' }}>
            <p>Sistema recomendador activo</p>
            <Switch
              checked={this.state.sysrec}
              color="primary"
              onChange={() => this.changeSysrecConfig(!this.state.sysrec)}
              value={`sysrec-enabled-${this.state.sysrec}`}
            />
          </div>
          <div style={{ border: '1px solid #5f7a80 ', borderRadius: '5px' }}>
            <Tooltip placement="top-start" title="Search">
              <SearchIcon
                className={classes.icon}
              />
            </Tooltip>
            <InputBase
              onClick={e => e.stopPropagation()}
              onChange={e => this.handleChange(e.target.value)}
              type="search"
              placeholder="Search…"
            />
          </div>
        </div>
        <div className={classes.titles}>
          <Typography className={classes.titleText}>

            User
          </Typography>
          <Typography className={classes.titleText}>

            Role
          </Typography>
          <Typography className={classes.titleText}>

            Max Projects
          </Typography>
        </div>
        <UserList />
      </CardPanel>
    );
  }
}

Dashboard.propTypes = {
  classes: PropTypes.any.isRequired,
  userType: PropTypes.string.isRequired,
  fetchAllUsers: PropTypes.func.isRequired,
  userSearch: PropTypes.func.isRequired,
};

function mapStateToProps(state) {
  return {
    userType: state.auth.accountType,
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchAllUsers,
    userSearch,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Dashboard));
