import { createSelector } from 'redux-orm';
import { filterText, removeDiacritics } from '../utils';
import orm from '../reducers/orm';

/* eslint-disable import/prefer-default-export */
export const allUsers = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.user.search,
  (session, search) => {
    if (!search) {
      return session.User.all().toRefArray();
    }
    const filteredSearch = filterText(removeDiacritics(search)).filter(Boolean);
    return session.User
      .filter(o => ((filteredSearch.every(a => removeDiacritics(o.account_type).includes(a)
      || !(!o.name || !removeDiacritics(o.name).includes(a))))))
      .toRefArray();
  },
);
